.promo {
  margin-top: -120px;
  overflow-x: hidden;
  background-image: url("../img/bg-desktop@1x.png");
  background-repeat: no-repeat;
  background-position: center bottom;
  background-size: cover;


  @media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
    background-image: url("../img/bg-desktop@2x.png");
  }

  @media (max-width: $tablet-width) {
    margin-top: -100px;
  }

  @media (max-width: $mobile-width) {
    background-image: url("../img/bg-tablet@1x.png");

    @media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
      background-image: url("../img/bg-tablet@2x.png");
    }
  }
}

.promo__wrapper {
  display: flex;
  flex-direction: column;
}

.promo__inner {
  position: relative;
  display: flex;
  flex-wrap: wrap;
  align-items: flex-start;

  min-height: 100vh;
  padding-top: 115px;
  padding-bottom: 150px;

  @media (max-width: $tablet-width) {
    min-height: 685px;
    padding-bottom: 100px;
  }

  @media (max-width: $mobile-width) {
    min-height: 645px;
    padding-bottom: 65px;
  }
}

.promo__description {
  position: relative;
  display: flex;
  flex-wrap: wrap;
  align-items: flex-start;
  width: 730px;
  margin-top: auto;
  margin-right: calc(100% - 730px);
  margin-bottom: auto;

  @media (max-width: $tablet-width) {
    width: 470px;
    padding-left: 65px;
    margin-top: 0;
  }

  @media (max-width: $mobile-width) {
    width: 100%;
    padding-left: 0;
  }
}

.promo__description h2 {
  margin-top: 0;
  margin-bottom: 0;
  padding-top: 145px;

  @include font(80, 94);
  font-weight: 700;
  font-family: $roboto-condensed;
  text-transform: uppercase;
  letter-spacing: 0.07em;

  @media (max-width: $tablet-width) {
    padding-top: 75px;

    @include font(60, 70);
  }

  @media (max-width: $mobile-width) {
    @include font(40, 47);
    padding-top: 190px;
  }
}

.promo__description p {
  width: 100%;
  position: relative;
  margin-top: 25px;
  margin-bottom: 25px;
  padding-left: 95px;

  @include font(22, 34);

  @media (max-width: $tablet-width) {
    padding-left: 50px;
    margin-bottom: 35px;

    @include font(18, 28);
  }

  @media (max-width: $mobile-width) {
    @include font(16, 25);
    margin-left: 0;
    margin-bottom: 25px;
  }

  &::before {
    content: "";
    position: absolute;
    top: 15px;
    left: 0;

    @include size(66, 2);
    background-color: $white;

    @media (max-width: $tablet-width) {
      @include size(33, 2);
    }
  }
}

.promo__image {
  position: absolute;
  top: 27.5%;
  left: 785px;

  @include size(705, 515);
  margin-top: 0;
  margin-left: 0;

  @media (max-width: $tablet-width) {
    top: 115px;
    left: 485px;

    @include size(465, 340);
  }

  @media (max-width: $mobile-width) {
    left: auto;
    right: -25px;
    top: 110px;

    @include size(301, 220);
  }

  &::before {
    content: "";
    position: absolute;
    top: -140px;
    left: -10px;

    @include size(671, 641);
    background-image: url("../img/bg-polygon.svg");
    background-repeat: repeat;
    background-position: center;

    @media (max-width: $tablet-width) {
      top: -150px;
      left: -70px;

      @include size(529, 506);

      background-size: 100% auto;
    }

    @media (max-width: $mobile-width) {
      top: -135px;
      left: 125px;

      @include size(505, 483);
    }
  }
}

.promo__image img {
  position: relative;
}

.promo__button {
  margin-top: 20px;
  margin-left: 95px;

  @media (max-width: $tablet-width) {
    margin-left: 50px;
  }


  @media (max-width: $mobile-width) {
    margin-left: 0;
  }
}

.promo__button span:last-child {
  display: none;

  @media (max-width: $mobile-width) {
    display: block;
  }
}

.promo__button span:first-child {
  @media (max-width: $mobile-width) {
    display: none;
  }
}

.promo__scroll {
  margin-top: auto;
  margin-left: 20px;

  @include font(14, 16);
  color: $white;
  transform: rotate(-90deg);
  transform-origin: left bottom;
  opacity: 0.5;

  &:hover,
  &:focus {
    opacity: 0.35;
    outline: none;
  }

  &:active {
    opacity: 0.2;
  }

  @media (max-width: $tablet-width) {
    display: none;
  }
}
